import React, { useState, useEffect } from 'react';
import { StyleSheet, Text, View, TouchableOpacity, ActivityIndicator, Platform } from 'react-native';
import { Camera } from 'expo-camera';


export const Testimonial: React.FC = () => {
  const [hasPermission, setHasPermission] = useState<null | boolean>(null);
  const [type, setType] = useState(Camera.Constants.Type.back);
  const [record, setRecord] = useState('');
  const cameraRef = React.useRef<null | any>(null);
  const VIDEO_CONFIG = {
    VideoQuality:['2160p'],
    maxDuration:10,
    maxFileSize:200,
    mute:false,
    // mirror: true,
    // flash: 'off',
    // type: 'back',
    // videoBitrate:5000000
  };
  const styles = StyleSheet.create({
    container: {
      flex: 1,
      alignItems: 'center',
      justifyContent: 'center',
    },
    camera: {
      flex: 1,
    },
    buttonContainer: {
      flex: 1,
      backgroundColor: 'transparent',
      flexDirection: 'row',
      margin: 20,
    },
    button: {
      flex: 0.1,
      alignSelf: 'flex-end',
      alignItems: 'center',
    },
    text: {
      fontSize: 18,
      color: 'white',
    },
    row: {
      flexDirection: 'row',
      justifyContent: 'center',
      flexWrap: 'wrap',
    },
    buttonBox: {
      padding: 32,
      alignItems: 'center',
      justifyContent: 'center',
      width: Platform.OS === 'web' ? 510 : '100%',
      backgroundColor: '#0abf53',
      borderRadius: 8,
    },
    buttonText: {
      fontWeight: 'bold',
      fontSize: 28
    },
  });

  useEffect(() => {
    (async () => {
      const { status } = await Camera.requestCameraPermissionsAsync();
      setHasPermission(status === 'granted');
    })();
  }, []);

  if (hasPermission === null) {
    return (
      <View style={styles.container}>
        <ActivityIndicator size={'large'} color="#0abf53" />
      </View>
    );
  }
  if (hasPermission === false) {
    return (
      <View style={styles.container}>
        <Text>Sem acesso a câmera.</Text>
      </View>
    );
  }

  const startRecord = () => {
    if (cameraRef) {
      try {
        cameraRef.current.recordAsync(VIDEO_CONFIG)
        .then((data: any) => {
          setRecord(data.uri);
        });
      } catch (error) {
        console.log(error);  
      }
    }
  };

  const stoptRecord = () => {
    if (cameraRef) {
      try {
        cameraRef.current.stopRecording(); 
      } catch (error) {
        console.log(error);
      }
    }
  };

  const saveVideo = async () => {
    console.log(`video: ${record}`)
  }
  return (
    <View style={styles.container}>
      <View style={styles.row}>
        <View style={styles.buttonBox}>
          <TouchableOpacity style={styles.button} onPress={() => startRecord()}>
            <Text style={styles.buttonText}> Iniciar Gravação</Text>  
          </TouchableOpacity>
          <TouchableOpacity style={styles.button} onPress={() => stoptRecord()}>
            <Text style={styles.buttonText}>Finalizar Gravação</Text>  
          </TouchableOpacity>
          <TouchableOpacity style={styles.button} onPress={() => saveVideo()}>
            <Text style={styles.buttonText}>Enviar vídeo</Text>  
          </TouchableOpacity>
        </View>
        <Camera 
            style={styles.buttonBox} 
            type={type}
            ref={cameraRef}
          >
            <View style={styles.buttonContainer}>
              <TouchableOpacity
                style={styles.button}
                onPress={() => {
                  setType(
                    type === Camera.Constants.Type.back
                      ? Camera.Constants.Type.front
                      : Camera.Constants.Type.back
                  );
                }}>
                <Text style={styles.text}> Flip </Text>
              </TouchableOpacity>
            </View>
          </Camera>
      </View>
    </View>
  );
}

